import { FSMContext, Point2D, SelfLinkCircle } from "../../types";
import { DrawableElement } from "./abstract";
import { HIT_TARGET_PADDING, NODE_RADIUS } from "./constants";
import { StateNode } from "./state_node";
import { draw_arrow, draw_text } from "./text_utils";

/** A circular link connecting a state to itself. */
export class SelfLink extends DrawableElement {
	public node: StateNode;
	public text: string;
	public anchorAngle: number;
	private mouseOffsetAngle: number;

	constructor(node: StateNode, mouse?: Point2D) {
		super();
		this.node = node;
		this.anchorAngle = 0;
		this.mouseOffsetAngle = 0;
		this.text = '';

		if (mouse) {
			this.set_anchor_point(mouse.x, mouse.y);
		}
	}

	set_mouse_start(x: number, y: number) {
		this.mouseOffsetAngle = this.anchorAngle - Math.atan2(y - this.node.y, x - this.node.x);
	};

	set_anchor_point(x: number, y: number) {
		this.anchorAngle = Math.atan2(y - this.node.y, x - this.node.x) + this.mouseOffsetAngle;
		// snap to 90 degrees
		const snap = Math.round(this.anchorAngle / (Math.PI / 2)) * (Math.PI / 2);
		if (Math.abs(this.anchorAngle - snap) < 0.1) this.anchorAngle = snap;
		// keep in the range -pi to pi so our containsPoint() function always works
		if (this.anchorAngle < -Math.PI) this.anchorAngle += 2 * Math.PI;
		if (this.anchorAngle > Math.PI) this.anchorAngle -= 2 * Math.PI;
	};

	private get_endpoints_and_circle(): SelfLinkCircle {
		const circleX = this.node.x + 1.5 * NODE_RADIUS * Math.cos(this.anchorAngle);
		const circleY = this.node.y + 1.5 * NODE_RADIUS * Math.sin(this.anchorAngle);
		const circleRadius = 0.75 * NODE_RADIUS;
		const startAngle = this.anchorAngle - Math.PI * 0.8;
		const endAngle = this.anchorAngle + Math.PI * 0.8;
		const startX = circleX + circleRadius * Math.cos(startAngle);
		const startY = circleY + circleRadius * Math.sin(startAngle);
		const endX = circleX + circleRadius * Math.cos(endAngle);
		const endY = circleY + circleRadius * Math.sin(endAngle);
		return {
			'hasCircle': true,
			'startX': startX,
			'startY': startY,
			'endX': endX,
			'endY': endY,
			'startAngle': startAngle,
			'endAngle': endAngle,
			'circleX': circleX,
			'circleY': circleY,
			'circleRadius': circleRadius
		};
	}

	draw(c: FSMContext, with_caret: boolean = false) {
		const stuff = this.get_endpoints_and_circle();
		// draw arc
		c.beginPath();
		c.arc(stuff.circleX, stuff.circleY, stuff.circleRadius, stuff.startAngle, stuff.endAngle, false);
		c.stroke();
		// draw the text on the loop farthest from the node
		const textX = stuff.circleX + stuff.circleRadius * Math.cos(this.anchorAngle);
		const textY = stuff.circleY + stuff.circleRadius * Math.sin(this.anchorAngle);
		draw_text(c, this.text, textX, textY, this.anchorAngle, this.selected, with_caret);
		// draw the head of the arrow
		draw_arrow(c, stuff.endX, stuff.endY, stuff.endAngle + Math.PI * 0.4);
	}

	contains_point(pos: Point2D) {
		const stuff = this.get_endpoints_and_circle();
		const dx = pos.x - stuff.circleX;
		const dy = pos.y - stuff.circleY;
		const distance = Math.sqrt(dx * dx + dy * dy) - stuff.circleRadius;
		return (Math.abs(distance) < HIT_TARGET_PADDING);
	}
}